import { useState, useEffect } from 'react'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogFooter } from '@/components/ui/dialog'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Loader2 } from 'lucide-react'
import { updateViatico } from '@/services/api'

interface Viatico {
    id: string
    fecha: string
    para?: string
    que_sustenta?: string
    tipo_comprobante?: string
    numero_documento?: string
    numero_comprobante?: string
    monto: number | string
    descripcion: string
}

interface EditViaticoDialogProps {
    viatico: Viatico | null
    open: boolean
    onOpenChange: (open: boolean) => void
    onSuccess: () => void
}

export function EditViaticoDialog({ viatico, open, onOpenChange, onSuccess }: EditViaticoDialogProps) {
    const [loading, setLoading] = useState(false)
    const [formData, setFormData] = useState<Partial<Viatico>>({})

    useEffect(() => {
        if (viatico) {
            setFormData({
                monto: viatico.monto,
                descripcion: viatico.descripcion,
                para: viatico.para || '',
                que_sustenta: viatico.que_sustenta || 'VIATICO',
                tipo_comprobante: viatico.tipo_comprobante || '',
                numero_documento: viatico.numero_documento || '',
                numero_comprobante: viatico.numero_comprobante || ''
            })
        }
    }, [viatico])

    const handleChange = (field: keyof Viatico, value: string | number) => {
        setFormData(prev => ({ ...prev, [field]: value }))
    }

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault()
        if (!viatico) return

        setLoading(true)
        try {
            await updateViatico(viatico.id, formData)
            onSuccess()
            onOpenChange(false)
        } catch (error) {
            alert('Error al actualizar viÃ¡tico: ' + (error as Error).message)
        } finally {
            setLoading(false)
        }
    }

    if (!viatico) return null

    return (
        <Dialog open={open} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-[425px]">
                <DialogHeader>
                    <DialogTitle>Editar ViÃ¡tico</DialogTitle>
                    <DialogDescription>
                        Modifica los detalles del viÃ¡tico. Haz clic en guardar cuando termines.
                    </DialogDescription>
                </DialogHeader>
                <form onSubmit={handleSubmit} className="grid gap-4 py-4">
                    <div className="grid grid-cols-4 items-center gap-4">
                        <Label htmlFor="monto" className="text-right">
                            Monto
                        </Label>
                        <Input
                            id="monto"
                            type="number"
                            step="0.01"
                            value={formData.monto}
                            onChange={(e) => handleChange('monto', e.target.value)}
                            className="col-span-3"
                            required
                        />
                    </div>
                    <div className="grid grid-cols-4 items-center gap-4">
                        <Label htmlFor="descripcion" className="text-right">
                            DescripciÃ³n
                        </Label>
                        <Textarea
                            id="descripcion"
                            value={formData.descripcion}
                            onChange={(e) => handleChange('descripcion', e.target.value)}
                            className="col-span-3"
                            required
                        />
                    </div>
                    <div className="grid grid-cols-4 items-center gap-4">
                        <Label htmlFor="para" className="text-right">
                            Para
                        </Label>
                        <Select value={formData.para as string} onValueChange={(val) => handleChange('para', val)}>
                            <SelectTrigger className="col-span-3">
                                <SelectValue placeholder="Seleccionar" />
                            </SelectTrigger>
                            <SelectContent>
                                <SelectItem value="EMPRESA">EMPRESA</SelectItem>
                                <SelectItem value="PERSONAL">PERSONAL</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>
                    <div className="grid grid-cols-4 items-center gap-4">
                        <Label htmlFor="tipo_comprobante" className="text-right">
                            Tipo Comp.
                        </Label>
                        <Select value={formData.tipo_comprobante as string} onValueChange={(val) => handleChange('tipo_comprobante', val)}>
                            <SelectTrigger className="col-span-3">
                                <SelectValue placeholder="Seleccionar" />
                            </SelectTrigger>
                            <SelectContent>
                                <SelectItem value="FACTURA">FACTURA</SelectItem>
                                <SelectItem value="BOLETA">BOLETA</SelectItem>
                                <SelectItem value="RECIBO POR HONORARIOS">RECIBO POR HONORARIOS</SelectItem>
                                <SelectItem value="SIN COMPROBANTE">SIN COMPROBANTE</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>
                    {formData.tipo_comprobante !== 'SIN COMPROBANTE' && (
                        <>
                            <div className="grid grid-cols-4 items-center gap-4">
                                <Label htmlFor="numero_documento" className="text-right">
                                    NÂ° Doc.
                                </Label>
                                <Input
                                    id="numero_documento"
                                    value={formData.numero_documento}
                                    onChange={(e) => handleChange('numero_documento', e.target.value)}
                                    className="col-span-3"
                                    placeholder="RUC o DNI"
                                />
                            </div>
                            <div className="grid grid-cols-4 items-center gap-4">
                                <Label htmlFor="numero_comprobante" className="text-right">
                                    NÂ° Comp.
                                </Label>
                                <Input
                                    id="numero_comprobante"
                                    value={formData.numero_comprobante}
                                    onChange={(e) => handleChange('numero_comprobante', e.target.value)}
                                    className="col-span-3"
                                    placeholder="Serie-NÃºmero"
                                />
                            </div>
                        </>
                    )}
                    <DialogFooter>
                        <Button type="submit" disabled={loading}>
                            {loading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                            Guardar cambios
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    )
}
